
.App {
  text-align: center;
  color: white;
  min-height: 100vh;
  font-family: Oxygen;

  background-image: linear-gradient(
    to bottom,
    #030C05,
    rgba(30, 71, 36, 0.5)
  ), url("./img/background.png");
  background-size:     cover;                 
  background-repeat:   no-repeat;
  background-position: center center;
}


.logo {

  position: relative;
  float: left;
  width: 500px;

}

.header {
  width: 100%;
  height: 200px;

}



.contador {
  
  display: flex;
  width: 100%;
  height: 50%;
  position: relative;
  justify-content: center;
  align-items: baseline;

  

}

.contador section {
  margin-top: auto;
  margin-bottom: auto;
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}



.contador span {

    padding-right: 60px;
    padding-left: 60px;
    font-size: 50px;
    padding-bottom: 30px;
    margin-top: auto;
    margin-bottom: auto;
}

.contador p {
  margin: 0px;

}

.numeroContador {
  font-size: 500%;
  font-weight:bold;
}

.letraContador {
  font-size: 24px;
  font-weight:normal;
}

.footer {
  height: 100px;
  margin: 0px;
  padding: 0px;
  position: absolute;
  bottom: 20px;
  right: 90px;

}

.footer a {
  font-size: 50px;
  margin-right: 60px;
  color: white;
  text-decoration: none;
}


@media only screen and (max-width: 800px) {


  .contador {
    height: 100%;
 

  }

    .contador section {
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
  }
    

    .numeroContador  {
      font-size: 200%;
      font-weight:bold;
    }

    .letraContador {
      font-size: 90%;
      font-weight:normal;
    }

    .contador span {

      padding-right: 0px;
      padding-left: 0px;
      font-size: 150%;

      margin-top: auto;
      margin-bottom: auto;
  }

  .logo {


    justify-content: center;
    width: 100%;
    float: none;


  }

  .footer {
    

    width: 100%;
    display: flex;
    padding: 0px;
    justify-content: center;
    bottom: 0;
    right: inherit;

  }

  .footer a {
    margin: auto;
  }

  
}